
<div :class="['s-toggle', toggleClass]">
  <button
    type="button"
    v-for="(val, key) in values"
    :key="val.id"
    :title="key | capitalize"
    @click="$emit('input', key)"
    :class="[
      's-toggle__option',
      { 's-toggle__option--active': value === key }
    ]"
    v-html="val"
  >
    {{ val }}
  </button>
</div>
