
<div>
  <transition-group name="fadeX-from-right" tag="div" class="notifications">
    <div
      v-for="{ id, msg, status } in visibleMessages"
      :key="`msg-${id}`"
      class="notification-msg"
      :class="{ 'notification-msg--error': status === 'error' }"
    >
      {{ msg }}
    </div>
  </transition-group>
</div>
